.all-slider {
    width: 100%;
    height: auto;
    margin-bottom: 70px;
}
.all-slider h1 {
    text-align: center;
    color: #000;
    font-weight: 500;
    margin-bottom: 10px;
}
.all-slider .slider {
    display: flex;
    justify-content: space-between;
}
.all-slider .card1.active,
.all-slider .card2.active,
.all-slider .card7.active,
.all-slider .card8.active {
    display: block;
}
.all-slider .card1 ,
.all-slider .card2 ,
.all-slider .card7 ,
.all-slider .card8  {
    border: none;
    display: none;
}
.all-slider .card1 img ,
.all-slider .card2 img ,
.all-slider .card3 img ,
.all-slider .card4 img ,
.all-slider .card5 img ,
.all-slider .card6 img ,
.all-slider .card7 img ,
.all-slider .card8 img {
    width: 100%;
    background-color: red;
    height: auto;
    margin-right: 10px ;
    border: none;
    border-radius: 0%;
    transition: all 2s;
}
.all-slider .butts {
    display: flex;
    width: 100%;
    height: auto;
    padding: 0 45% ;
}
.all-slider .butt1 ,
.all-slider .butt2 {
    width: 30px;
    height: 10px;
    border: 2px solid #000;
    margin-right: 3px;
}
.all-slider .butt1.active ,
.all-slider .butt2.active {
    background-color: #000;
}
