

/* start footer */
footer {
    background-color: var(--blue-color);
    padding: 50px;
}

.allfooter {
    display: flex;
    margin-bottom: 20px;
}

.def {
    width: 30%;
}

.def img {
    width: 200px;
    margin-bottom: 20px;
}

.def p {

    color: #fff;
    font-size: 14px;
}

.service {
    margin: 0 50px;
    width: 35%;
}

.service h3 {
    color: #fff;
}

.service ul {
    columns: 2;

}

@media (max-width :790px) {
    .service ul {
        columns: 1;
    }

    .service {
        margin: 0px;
    }

    .allfooter {
        display: block;
    }
}

.service ul li,
a {
    list-style: none;
    color: #fff;
    transition: all 0.3s;
}

.service ul li a:hover {
    color: var(--green-color);
    text-decoration: none;
}

.contact h3,
p {
    color: #fff;
}

.contact {
    width: 35%;
}

.iconsfooter {
    display: flex;
    margin: auto;
    width: 150px;
}

.iconsfooter div {
    margin: 0 10px;
}

.iconsfooter div i {
    color: aliceblue;
    border: 1px solid #fff;
    border-radius: 50%;
    font-size: 20px;
    padding: 7px;
}

hr {
    height: 1px;
    background-color: #f1f1f1;
}

/* end footer */