.allcontact {
    width: 100%;
    height: 180px;
    position: fixed;
    padding-right: 30px;
    top: 70%;
    z-index: 2000;
}

.contactside {
    width: 50px;
    height: auto;
    background-color: #fff;
    padding: 6px;
    border-radius: 15px;
    border: 0.1px solid #d2d2d274;
    position: relative;
    float: right;
}

.contactside img {
    width: 40px;
    margin: 5px 0;
    cursor: pointer;
}
