.section {
    margin: 50px 0;
}
.section h1 {
    color: #000;
    text-align: center;
    font-weight: 500;
    margin-bottom: 40px;
}
.section p {
    color: #000;
}
.section span {
    font-weight: 800;
}
.section a {
    text-decoration: underline;
    color: #000;
}

/* data */
.data2  {
    display: flex;
    width: 100%;
    height: auto;
    margin: 50px 0;
}
.div111  {
    width: 50%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}
.div111 img {
    width: 100%;
    margin: 30px 10px;
}
.div111 p {
    color: #000;
}
.div111 a {
    color: #000;
    text-decoration: underline;
}
.div111 span {
    font-weight: 700;
}
.div22  {
    width: 50%;
    padding: 7px;
    height: auto;
}
.div22 p {
    color: #000;
}
.div22 p a {
    color: #000;
    text-decoration: underline;
}
.div22 p span {
    font-weight: 600;
}
.div22  button {
    background-color: var(--green-color);
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    color: #fff;
    margin: 0 30%;
}
.div22 .a   {
    margin-top: 70px;
} 
.div22 .a ul {
    list-style: none;
}
.div22 .a ul li {
    font-style: normal;
    color: aqua;
}
.div22 .a a {
    color: #000;
    display: block;
    margin-bottom: 20px;
    text-align: center;
}
.div22 .a a:hover {
    color: var(--blue-color);
    text-decoration: none;
}
.fin {
    margin: 50px 0;
}
.fin h1  {
    text-align: center;
    font-size: 50px;
    color: #000;
}
.fin p {
    color: #000;
}
.fin p span {
    font-weight: 600;
}
.fin p a {
    text-decoration: underline;
    color: #000;
}
