.midle {
    margin-bottom: 70px;
    position: relative;
}

.midle .over {
    background-color: #000000c7;
    position: absolute;
    width: 100%;
    height: 350px;
    z-index: 111;
}

.midle .over .all-over {
    display: flex;
    justify-content: space-between;
}

.midle .over .all-over .opacity1 img,
.midle .over .all-over .opacity2 img,
.midle .over .all-over .opacity3 img {
    width: 25%;
    height: auto;
    margin: 20% 40% 10px 40%;

}
.midle .over .all-over .opacity1 h2,
.midle .over .all-over .opacity2 h2,
.midle .over .all-over .opacity3 h2  {
    text-align: center;
    color: #fff;
}
.midle .over .all-over .opacity1 p,
.midle .over .all-over .opacity2 p,
.midle .over .all-over .opacity3 p {
    text-align: center;
    color: #727272;
}

.midle .over .all-over .opacity1,
.midle .over .all-over .opacity2,
.midle .over .all-over .opacity3 {
    width: 30%;
    height: auto;
}

.midle img {
    width: 100%;
    height: 350px;
}
