.carousel .carousel-one,
.carousel .carousel-two  {
    width: 100%;
    height: 60vh;
    transition: all 1s;
    margin-bottom: 70px;
}

.carousel .carousel-one .div1 {
    background: url(../../Assets/slider1.png);
    background-size: cover;
    /* width: 100%; */
    height: 60vh;
    position: absolute;
} 
.carousel .carousel-one .div2 {
    position: absolute;
    width: 100%;
    height: 60vh;
    background-color: rgba(0, 0, 0, 0.701);
    z-index: 66;
    padding: 10% 0 0 0;
}
.carousel .carousel-one .div2 h1 {
    text-align: center;
    margin: auto;
    margin-bottom: 30px;
    font-size: 50px;
    color: #fff;

}
.carousel .carousel-one .div2 a {
    margin: 10% 0 0 45% ;
    text-decoration: none;
    font-style: normal;
    color: #fff;
    background-color: var(--green-color);
    padding: 15px 30px;
    border-radius: 5px;
}

.carousel .carousel-two .div1 {
    background: url(../../Assets/slider2.webp);
    background-size: cover;
    /* width: 100%; */
    height: 60vh;
    position: absolute;
    
} 
.carousel .carousel-two .div2 {
    position: absolute;
    width: 100%;
    height: 60vh;
    background-color: rgba(0, 0, 0, 0.701);
    z-index: 66;
    
    padding: 10% 0 0 0;
}
.carousel .carousel-two .div2 h1 {
    text-align: center;
    margin: auto;
    margin-bottom: 30px;
    font-size: 50px;
    color: #fff;

}
.carousel .carousel-two .div2 a {
    margin: 10% 0 0 45% ;
    text-decoration: none;
    font-style: normal;
    color: #fff;
    background-color: var(--green-color);
    padding: 15px 30px;
    border-radius: 5px;
}
/* end show slider */