.all-contact {
    width: 100%;
    height: auto;
    display: flex;
    padding: 0 10px;
    margin: 20px ;
}
.all-contact .div1  {
    width: 60%;
    height: auto;
} 
.all-contact .div1 p {
    color: #000;
}

/* form */
label {
    display: block;
}
input , select {
    width: 50%;
}
form .btn {
    background-color: #001B5B;
    color: #fff;
}
/* div2  */
.all-contact .div2  {
    width: 30%;
    height: auto;
    padding: 0 0 0  30px   ;
} 
.all-contact .div2 p i {
    color: #000;
    margin: 0%;
}
.all-contact .div2 p {
    color: #000;
}
.all-contact .div2 p span  {
    font-weight: 800;
}
.clock {
    display: flex;
}
.all-contact .div2 a {
    color: #000;
    display: block;
}
.all-contact .div2  button {
    background-color: red;
    padding: 10px;
    border: none;
    border-radius: 10px;
    color: #fff;
    margin: 10px 20%;
}
.all-contact .div2  button:hover {
    background-color: var(--green-color);
}