.tit {
    margin: 50px 0;
}

.tit h1 {
    color: #000;
    font-weight: 700;
    margin-bottom: 30px;
}

.tit p {
    color: #000;
}

.alldata {
    display: flex;
    padding: 20px;
}

.alldata .div1 {
    width: 50%;
    height: auto;
}

.alldata .div1 img,
.alldata .div2 img {
    width: 80%;
}

.alldata .div1 p,
.alldata .div2 p {
    width: 80%;
    color: #000;

    margin: 20px 0;
}

.alldata .div2 {
    width: 50%;
    height: auto;
}

.alldata .div2 ul {
    padding: 20px;
}

.alldata .div2 ul li {
    color: #000;
}

.fin {
    margin: 50px 0;
}

.fin h1 {
    color: #000;
    text-align: center;
    font-size: 45px;
    font-weight: 600;
    margin-bottom: 30px;
}

.fin p {
    color: #000;
}