.div12 {
    display: flex;
    margin-bottom: 50px;
    justify-content: space-around;
}

.div12 .data1 {
    width: 50%;
}

.div12 h3 {
    font-size: 30px;
    margin-bottom: 20px;
}
.div12 h3,
.div12 p {
    color: #000;
}

.div12 a {
    color: #000;
    text-decoration: underline;
    font-size: 16px;
}

.div12 span {
    font-style: italic;
    font-weight: 800;
}

.div21 {
    margin-bottom: 70px;
}

.div21 p {
