}
.dropnav-item {
    padding: 2px 5px;
    width: 320px;
    list-style: none;
}
.dropnav-item:hover  {
    background-color: var(--blue-color);
}
.dropnav-item:hover a {
    
    color: white;
}

.li-item {
    list-style: none;
}