/* start var */
:root {
  --green-color: #9bbf3e;
  --blue-color: #001B5B;
}

/* end var */

/* ================================================= */

/* start* */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: #727272;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
