header {
    box-shadow: 10px 0 14px rgb(97, 97, 97);
}
.logo  img {
    width: 200px;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}
nav .ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    align-items: center;
    padding: 10px 0;
}
.DropMenu ul  {
    display: none;
    width: auto;
    position: absolute;
    border: 0.1px solid #b6b6b6b8;
    padding: 0;
    margin-top: 0;
    background-color: #fff;
    z-index: 77967687;
    transition: all .8s;
}

a {
    text-decoration: none;
    color: #000;
    font-size: 20px;
    font-weight: 600;
}
a:hover {
    color: var(--blue-color);
}


.DropMenu a {
    font-size: 16px;
    color: #000;
}


.Menuicon i {
    font-size: 40px;
    cursor: pointer;
    display: none;
}

.xmark i {
    font-size: 40px;
    cursor: pointer;

}

.xmark {
    display: none;
}

/* media  */


@media (max-width :1280px) {
    nav {
        width: 100%;
    }

    .Menuicon i {
        display: block;
    }
    nav .ul {
        display: none;
    }
    nav .ul2 {
        position: absolute;
        top: 100%;
        right: 0;
        width: 100%;
        height: auto;
        background-color: #fff;
        display: none;
        flex-direction: column;
        padding: 0 0 10px 10px;
        align-items: flex-start;
        overflow: scroll;
        z-index: 77967687;
    }


}