

.card {
    width: 250px;
    height: auto;
    margin: 10px 10px;
    border-radius: 0%;
    box-shadow: 1px 1px 10px #888;
}

.card img {
    width: 100%;
    height: 170px;
    margin-bottom: 5px;
}
.card a {
    color: var(--blue-color);
    font-size: large;
    font-weight: 400;
    margin: 0 0 10px 3px;
    font-style: italic;
}
.card p {
    font-size: 12px;
    color: #000;
    margin: 0 0 6px 3px;
}

.card p span {
    font-weight: bolder;
    color: #000;
}

.card .voir {
    margin: 0 35%;
    font-size: 14px;
    cursor: pointer;
    font-style: normal;
}